import React ,{Component} from 'react';
import { Breadcrumb,Row,Form,Input,Button,Col,message} from 'antd'
import '../../style/personInformation/baseInformation.scss'
import axios from 'axios'
import { Link } from 'react-router-dom'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class InformationForm extends Component{
      constructor(props){
        super(props)
        this.state={
              info:[],
        }
      }
      getInformation=()=>{
        // var id=localStorage.getItem('login');
        // console.log(id);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/staff/my',{
            name:user,
            num:loginid,
          })
          .then((res)=>{
            console.log(res);
            this.setState({
                 info:res.data.data
            })
          
       }).catch((error)=>{
           console.log(error);
       })
      }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log(': ', values);
         
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
              console.log(loginid,user);
              if(loginid!='' && user!=""){
              axios.post('/index.php/staff/change',{
                  value:values,
                   name:user,
                   num:loginid,
              })
              .then((res)=>{
                    if(res.data.code==200){
                      console.log(res);
                       message.success('')
                    }else if(res.data.code==501){
                      message.error('')
                    }
              }).catch((error)=>{
                console.log(error)
              })
            }




          }
        });
      };
      componentDidMount(){
            this.getInformation();
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { info } =this.state;
        return(
          <div className="personInformation">
              <Breadcrumb>
                  <Breadcrumb.Item ><Link to="/personInformation/baseInformation"> </Link></Breadcrumb.Item>
                  <Breadcrumb.Item><Link to="/personInformation/baseInformation"></Link> </Breadcrumb.Item>
                </Breadcrumb>
            <div className="base">
                <h3 className="title"></h3>
                <br style={{color:'rgba(237,237,237,1)'}}/>
               
                  <ul className="ulbase">
                    <li><span></span>{info.name}</li>
                    <li><span></span>{info.sex}</li>
                    <li><span></span>{info.card_id}</li>
                    <li><span></span>{info.education}</li>

                    <li><span></span>{info.profession}</li>
                     <li><span></span>{info.city}</li>
                     <li><span></span>{info.department}</li>
                     <li><span></span>{info.job}</li>

                     <li><span></span>{ info.r_time}</li>
                     <li><span></span>{ info.address}</li>
                     <li><span></span>{info.birthday}</li>
                     <li><span></span>{info.urgent }</li>
                   
                  </ul>
            </div>
            <div className="family">
                <h3 className="title"></h3>
                
                <p><span></span>{info.huji}</p>
                <h3 className="ptitle"></h3>
                  <ul className="ulfamily">
                    <li><span></span>{info.name1}</li>
                    <li><span></span>{info.tel1}</li>
                    <li><span></span>{info.birth1}</li>
                    <li><span></span>{info.name2}</li>
                     <li><span></span>{info.tel2}</li>
                     <li><span></span>{info.birth2}</li>
                  </ul>
            </div>
            {/* <div className="box"> */}
            <br/>
           <h5 className="tit"></h5>
           <br/>
           <br/>
           <br/>
           <br/>
            <Form  layout="inline"  onSubmit={this.handleSubmit} >
            <Row gutter={24}>
              <Col  span={8}>
            <Form.Item label="" >
            {getFieldDecorator('old', {
              //  initialValue:newsdetail.title
                })(
               <Input placeholder="" style={{ width:200}}/>
                )}
            </Form.Item >
            </Col>
            <Col span={8}>
            <Form.Item label="" >
            {getFieldDecorator('new', {
              //  initialValue:newsdetail.title
              rules: [
              {pattern:/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{0,8}$/,message:'8'}
                ],
                })(
               <Input placeholder="" style={{ width:200}}/>
                )}
            </Form.Item >
            <p>8</p>
            </Col>
        

            <Form.Item  >
                <Button type="primary" htmlType="submit" className="sub">
                  
              </Button>
            </Form.Item >
            </Row>
            </Form>
            {/* </div> */}
           
           
          </div>
          )
      }
}
const personInformation = Form.create()(InformationForm);
export default personInformation