import React ,{Component} from 'react';
import{Breadcrumb,Form,Input,Button,Table,LocaleProvider } from 'antd'
import axios from 'axios'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import '../../../style/statistics/caipanlist.scss'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class caidetailForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        //
        if(loginid!='' && user!=""){
        axios.post('/index.php/pro/index',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            //  console.log(res);
             var number=res.data
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     projectname:v.building_name,
                     cainum:'44',
                     kaonum:'35',
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
          }else{
             console.log("")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
     //
      }
      
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '',
            dataIndex: 'projectname',
          },
          {
            title: '',
            dataIndex: 'cainum',
          },
          {
            title: '',
            dataIndex: 'kaonum',
          },
          {
            title: '',
            dataIndex: 'handle',
            render:(text ,record) => <div><span style={{color:"#1890FF",cursor:"pointer"}}></span>&nbsp;<span style={{color:"#1890FF",cursor:"pointer"}}></span></div>
          }

        ];
        const rowSelection = {
          onChange: (selectedRowKeys, selectedRows) => {
            console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
          },
          getCheckboxProps: record => ({
            disabled: record.name === 'Disabled User', // Column configuration not to be checked
            name: record.name,
          }),
        };
        const paginationProps = {
          showSizeChanger: true,
          showQuickJumper: {goButton:<Button type="primary"></Button>},
          showTotal: ((total,count) => {
            console.log(count);
             return ` ${total} `;
          }),
          // showTotal: () => `${totals}`,
          // pageSize: this.state.pageSize,
          // current: page.pageNum,
          // total: page.total,
          // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
          // onChange: (current) => this.changePage(current),
        };
        return(
          <LocaleProvider locale={zhCN}>
          <div className="caipanlist">
            <Breadcrumb>
              <Breadcrumb.Item href="/"> </Breadcrumb.Item>
              <Breadcrumb.Item href="/"></Breadcrumb.Item>
              <Breadcrumb.Item href="">  </Breadcrumb.Item>
          </Breadcrumb>
          <Form layout="inline">
            <Form.Item label="">
                {getFieldDecorator('projectname',{
                    rules: [{ required: false, message: '' }],
                })(
                  <Input placeholder="" style={{ width:150}}/>
                )}
            </Form.Item>
            <Form.Item>
                 <Button type="primary" style={{marginRight:'944px'}}></Button>
            </Form.Item>
            <Form.Item>
                <span className="num"><i>44</i></span>
                <span className="num"><i>39</i></span>
            </Form.Item>
          </Form>
              <div className="tablelist">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   loading={this.state.loading}  pagination={ paginationProps } />
                     {/* <p className="yema">&nbsp;<span>400&nbsp;</span>&nbsp; &nbsp;<i style={{fontStyle:'normal'}}>&nbsp;1/50</i>&nbsp;</p> */}
              </div>
          
          </div>
          </LocaleProvider>
          )
      }
}
const caipanList=Form.create()(caidetailForm);
  export default caipanList