import React ,{Component} from 'react';
import {Form, Input, DatePicker ,Row,Col , Select}  from 'antd'
const {Option } =Select
const { TextArea } = Input;
function onChange(value, dateString) {
  // console.log('Selected Time: ', value);
  // console.log('Formatted Selected Time: ', dateString);
}

function onOk(value) {
  // console.log('onOk: ', value);
}

class modeform  extends Component{
      constructor(props){
        super(props)
      }
   
      render(){
        const { getFieldDecorator } = this.props.form;
        return( 
          <div className="news">
               <Form layout="inline"> 
           <Row  gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('code', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Input style={{width:180}} placeholder=""/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('zongjia', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Input placeholder="" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('jianmian', {
                })(
                   <Input placeholder="" style={{width:180}}/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('loujia', {
                })(
                    <Input placeholder="" style={{width:180}}/>
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('unit', {
                
                })(
                    <Input placeholder="" style={{width:180}}/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('time', {
                })(
                    <DatePicker showTime placeholder="" onChange={onChange} onOk={onOk} style={{width:170}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('bian', {
                })(
                    <Input  style={{width:180}} placeholder=""/>
                        
                  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('address', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Input placeholder="" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>
               </Form>
          </div>
          )
      }
}
const modeforms=Form.create()(modeform)
  export default modeforms