import React ,{Component} from 'react';
import {Form, Input, DatePicker ,Row,Col , Select}  from 'antd'
const {Option } =Select
const { TextArea } = Input;
function onChange(value, dateString) {
  // console.log('Selected Time: ', value);
  // console.log('Formatted Selected Time: ', dateString);
}

function onOk(value) {
  // console.log('onOk: ', value);
}

class modeform  extends Component{
      constructor(props){
        super(props)
      }
   
      render(){
        const { getFieldDecorator } = this.props.form;
        return( 
          <div className="news">
               <Form layout="inline"> 
           <Row  gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('tag', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Select style={{width:180}}>
                        <Option value='11'>ABC</Option>
                        <Option value='22'>22</Option>
                        <Option value='33'>33</Option>
                        <Option value='44'>44</Option>
                    </Select>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('name', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Input placeholder="" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('pro', {
                })(
                   <Input placeholder="" style={{width:180}}/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('or', {
                })(
                    <Select style={{width:180}}>
                        <Option value='11'>1</Option>
                        <Option value='22'>22</Option>
                        <Option value='33'>33</Option>
                        <Option value='44'>44</Option>
                    </Select>
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('rtime', {
                
                })(
                    <DatePicker showTime placeholder="" onChange={onChange} onOk={onOk} style={{width:170}} />
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('ztime', {
                })(
                    <DatePicker showTime placeholder="" onChange={onChange} onOk={onOk} style={{width:170}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('ttime', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <DatePicker showTime placeholder="" onChange={onChange} onOk={onOk} style={{width:170}} />
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="" >
                {getFieldDecorator('r', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Input placeholder="" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="" >
                {getFieldDecorator('wpeople', {
                    rules: [{ required: false, message: '!' }],
                })(
                    <Input  placeholder="" style={{width:180}} />
  
                )}
                </Form.Item>
            </Col>
            
        </Row>

       
               </Form>
          </div>
          )
      }
}
const modeforms=Form.create()(modeform)
  export default modeforms